











































import AsModal from "@/mixins/AsModal";
import {TransducerDB} from "@/types/ApiTypes";
import {Component, Emit, Mixins} from "vue-property-decorator";

@Component
export default class AddOTAModal extends Mixins(AsModal) {
  @Emit("refresh") refresh(): void {}

  formData: TransducerDB = {category: "", content: ""};
  mockFormData = {};

  add(): void {
    this.api.addTransducerDB(this.formData).then(() => {
      this.refresh();
      this.clean();
    });
  }

  clean(): void {
    this.formData = {category: "", content: ""};
    this.dismiss();
  }
}
