



















import {TransducerDB} from "@/types/ApiTypes";
import {Component, Vue} from "vue-property-decorator";

@Component
export default class ActuatorTable extends Vue {
  data: TransducerDB[] = [];

  mounted(): void {
    this.getData();
  }

  getData(): void {
    this.api.getTDBList({query: "category:Speaker"}).then((res) => {
      this.data = res;
    });
  }
}
